

































































































import Vue from "vue";
import {ListAllChats , ListChatMsgs , CreateMsg} from '@/repositories/chat'
import {inbox , msg , msgRequest} from "@/models/chat";
import {mapGetters} from 'vuex'
import router from '@/router'
import { addParamsToLocation } from "@/utils/helpers";
export default Vue.extend({
  name: "users-list",
  components: {},
  data() {
    return {
      loading: false,
      msg:'',
      chatLoading:false,
      chatsLoading:false,
      chats : [] as inbox[],
      currentChat : null as inbox|null,
      msgs : [] as msg[]
    };
  },
   computed:{
     ...mapGetters({
      user: 'user/user'
    })
  },
  methods: {
    edit() {
      const currentPath = router.currentRoute.path;
      const path = currentPath.replace("view", "edit");
      router.push(path);
    },
    listChats(){
      this.chatsLoading = true
      ListAllChats().then((res) => {
        this.chats = res
        this.chatsLoading = false
      })
    },
    selectChat(chat:inbox){
      this.currentChat = chat
      addParamsToLocation({id : chat.Id} , router.currentRoute.path)
      this.findChatMsgs(chat.Id)
    },  
    findChatMsgs(id:number){
      this.chatLoading = true
      ListChatMsgs(id).then((res:msg[]) => {
        this.msgs = res
        this.chatLoading = false
        const elem = document.querySelector(".chat-msgs")!;
        elem.scrollTop = elem.scrollHeight;
      })
      
    },
    send() {
      const payload :msgRequest= {
        ToId : this.currentChat!.Id,
        Msg : this.msg,
      }
      CreateMsg(payload).then((res : any) => {
        const msg : msg = {
          Id : res,
          Msg : this.msg,
          Mine : true,
          Name : '',
          CreatedAt : new Date().toString(),
          Seen : false,
        }
        this.msgs.push(msg)
        this.msg = ''
      })
    },
  },
  mounted() {
    this.listChats()
    if (router.currentRoute.query.id) this.findChatMsgs(parseInt(router.currentRoute.query.id as string))
  },
});
